import React from 'react';


class BadgeForm extends React.Component {
    //inicializamos el state
 /*    state = {}; */
    //metodo para manejar los eventos del boton con onClick
    //handleClick = event => {
     //   console.log('Button was clicked');
    //}
    //metodo para manejar los eventos del boton con onSubmit
    //handleSubmit = event => {
    //    /* Si no queremos que el form se envie, lo frenamos con preventDefault */
    //    event.preventDefault();
    //    console.log(this.state);
    //}


    render() {
       return(  <div className="">
                
                <form onSubmit={this.props.onSubmit} >
                    <div className="form-group">
                        <label >First Name</label>
                        <input className='form-control' type="text" name='firstName' onChange={this.props.onChange} value={this.props.formValues.firstName} />
                    </div> 
                    <div className="form-group">  
                        <label >Last Name</label>
                        <input className='form-control' type="text" name='lastName' onChange={this.props.onChange} value={this.props.formValues.lastName}/>
                    </div> 
                    <div className="form-group">
                        <label >Email</label>
                        <input className='form-control' type="mail" name='email' onChange={this.props.onChange} value={this.props.formValues.email}/>
                    </div> 
                    <div className="form-group">
                        <label >Job Title</label>
                        <input className='form-control' type="text" name='jobTitle' onChange={this.props.onChange} value={this.props.formValues.jobTitle}/>
                    </div> 
                    <div className="form-group">
                        <label >Twitter</label>
                        <input className='form-control' type="text" name='twitter' onChange={this.props.onChange} value={this.props.formValues.twitter}/>
                    </div>
                 {/*
                 podemos manejar el boton con un evento onClick
                 <button className="btn btn-primary" type='button' onClick={this.handleClick}>Save</button>  */}
                 {/* O tambien con un evento onSubmit, siempre declarando el metodo que manejara el evento */}
                    <button className="btn btn-primary" onClick={this.handleClick} >Save</button>
                    {/* //Si hay algun error en this.props, lo mostramos */}
                {this.props.error && (<p className='text-danger'>{this.props.error.message}</p>)}
                </form>
                </div> 
    )
    }



}

export default BadgeForm;