import React from 'react';
import '../components/styles/BadgesList.css';
import {Link} from 'react-router-dom';
import Gravatar from './Gravatar.js';

class BadgesListItem extends React.Component {
    render() {
      return (
        <div className="BadgesListItem">
          <Gravatar
            className="BadgesListItem__avatar"
            email={this.props.badge.email}
          />
  
          <div>
            <strong>
              {this.props.badge.firstName} {this.props.badge.lastName}
            </strong>
            <br />@{this.props.badge.twitter}
            <br />
            {this.props.badge.jobTitle}
          </div>
        </div>
      );
    }
  }

//custom hook para filtrar badges
function useSearchBadges(badges) {
  const [query,setQuery] = React.useState('');
  //guardamos el dato del filtro que aplicamos a los badges en funcion del query
  const [filteredBadges,setFilteredBadges] =React.useState(badges)  
  React.useMemo(()=>{
    const result = badges.filter(badge => {
   return (badge.firstName.toLowerCase().includes(query.toLowerCase())) || (badge.lastName.toLowerCase().includes(query.toLowerCase())) || (badge.jobTitle.toLowerCase().includes(query.toLowerCase())) || (badge.twitter.toLowerCase().includes(query.toLowerCase())) || (badge.email.toLowerCase().includes(query.toLowerCase()));
    });
  setFilteredBadges(result)
  },[badges, query]);

  return {query, setQuery,filteredBadges};
}

function BadgesList (props) {

    const badges = props.badges;
    //hook para useState y poder escribir en el input para filtrar los badges
    const {query, setQuery, filteredBadges} = useSearchBadges(badges);

    if (filteredBadges.length === 0) {
                return(
                  
                    <div className="">
                    <div className="form-group">
                    <label>Filter Badges</label>
                    <input type="text" className="form-control" value={query} onChange={(event)=> {
                    setQuery(event.target.value);
                    }}/>
                </div>  
                    <h3>No badges found</h3>
                    <Link className="btn btn-primary" to="/badges/new">
                    Create new Badge</Link>
                    </div>
                )
    }
  
            return (
              <div className="BadgesList">
                <div className="form-group">
                  <label>Filter Badges</label>
                  <input type="text" className="form-control" value={query} onChange={(event)=> {
                    setQuery(event.target.value);
                  }}/>
                </div>
                
                <ul className="list-unstyled">
                {/* usamos map para iterar cada objeto dentro de data y traer la info */}
                {filteredBadges.map((badge)=> {
                
                return (/* Cada vez que iteramos y manejamos una lista, 
                    debemos indicarle a React un key que contendra un ID,
                     para que React se de cuenta de los cambios que puedan ocurrir */
                    <li key={badge.id}>
                        <Link className='text-reset text-decoration-none' to={`badges/${badge.id}`}>
                        <BadgesListItem badge={badge} />
                        </Link>
                    </li>
                )
            })}
            </ul>
            </div>
                
            )
        
}

export default BadgesList;