//Importamos React
import React from 'react';
//importamos el archivo CSS
import './styles/Badge.css';
import 'bootstrap/dist/css/bootstrap.css';
import confLogo from '../images/badge-header.svg';
import Gravatar from '../components/Gravatar.js'; 
/* creamos un componente, el cual extiende de la clase React.Component */

class Badge extends React.Component {
    
//Todos los componentes requieren al menos un metodo obligatorio, el render
render() {
    const {firstName, lastName, twitter, jobTitle} = this.props;
    
    const element = (
    <div className ="Badge"> 
            <div className = "Badge__header">
                 <img src={confLogo} alt="logo"></img>
            </div>
            <div className = "Badge__section-name">
                <Gravatar className='Badge__avatar' email={this.props.email} />
            <h1>{firstName} <br/> {lastName}</h1>  
            </div >
            <div className ="Badge__section-info">
                <h3>{jobTitle}</h3>
                <div>@{twitter}</div>
            </div>
            <div className ="Badge__footer">
                <p>#PlatziConf</p>
            </div>
    </div>
    )
return element;
}

}


//Exportamos el componente
export default Badge;


          