//Importamos React
import React from 'react';
import Badge from '../components/badge.js';
import header from '../images/platziconf-logo.svg';
import BadgeForm from '../components/BadgeForm.js';
import '../components/styles/BadgeEdit.css';
import api from '../Api.js';
import PageLoading from '../components/PageLoading.js';
class BadgeEdit extends React.Component {
    //inicalizamos un state para guardar los datos de Badge
    state = {
        loading: true,
        error: null,
        form: {
        firstName: '',
        lastName: '',
        jobTitle:'',
        twitter:'',
        email:'',
    }};

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async event => {
        this.setState({
            loading: true,
            error:null,
        });

        try{
            const data = await api.badges.read(
                this.props.match.params.badgeID);
                
            
            this.setState({
                loading: false,
                form: data,
                
            });
            
        }
        catch(error) {
            this.setState({
                loading: false,
                error: error,
            });
        }
    }

    //generamos un metodo handleChange para manejar los cambios del form
    handleChange = event => {
        this.setState({//seteamos el estado del form.
            form: {
                //usamos un spread operator para recibir los argumentos multiples
                /* El operador de propagación spread operator permite que una expresión sea expandida en situaciones 
                donde se esperan múltiples argumentos (llamadas a funciones) o múltiples elementos (arrays literales). */
                ...this.state.form,
                [event.target.name]: event.target.value,
            }
        })
    }

    handleSubmit = async (event) => {
        //frenamos el form para evitar que recargue
        event.preventDefault();
        this.setState({loading: true, error: null});
        try{
            await api.badges.update(this.props.match.params.badgeID, this.state.form);
            this.setState({loading: false,});
            //redirigimos al usuario hacia badges
            this.props.history.push('/badges');
        }
        catch(error) {
            this.setState({
                loading: false,
                error: error,
            });
        }
    }

    render() {
if (this.state.loading) {
    //cargamos el loading para cuando se guarde el nuevo badge
    return <PageLoading />;
}


        return (
        <React.Fragment>
            <nav>
                <div className='BadgeEdit__hero'>
                    <img className='img-fluid BadgeEdit__hero-image' src={header} alt='Hero'></img>
                </div>
                <div className='container'>
                    <div className='row'>
                        <div className='col-6'>
                        <Badge  firstName = {this.state.form.firstName || 'First Name'}
                                lastName = {this.state.form.lastName || 'Last Name'}
                                jobTitle = {this.state.form.jobTitle || 'Job Title'}
                                twitter = {this.state.form.twitter || 'Twitter'}
                                email = {this.state.form.email || 'Email'}/>    
                        </div>   
                        <div className='col-6'>
                        <h1>Edit Attendant</h1>
                            {/* Pasamos el metodo handleChange como un prop para que badgeForm lo pueda usar */}
                            <BadgeForm onChange={this.handleChange} 
                            onSubmit={this.handleSubmit} 
                            formValues={this.state.form} 
                            error={this.state.error} />
                        </div>
                    </div>    
                </div>                
            </nav>
            </React.Fragment>

        )
    }
}

export default BadgeEdit;