//Importamos React
import React from 'react';
import BadgesList from '../components/BadgesList.js';
import {Link} from 'react-router-dom';
import '../components/styles/Badges.css';
import confLogo from '../images/badge-header.svg';
import Api from '../Api.js';
import PageLoading from '../components/PageLoading.js';
import PageError from '../components/PageError.js';

class Badges extends React.Component {
    //data para el ejemplo
     //metodo constructor, va en primer lugar
      //necesitamos pasarle props para inicializar la superclase
        /* console.log(`1.constructor`) */
          //inicializamos estado
          state = {
              //generamos un elemento loading para mostrar al usuario cuando estemos cargando la data desde la API
            loading: true,
            data: undefined,
            error: null
          };
      
      //metodo componentDidMount va en tercer lugar
       componentDidMount() { //Vamos a hacer la peticion a la API aqui, una vez que los datos estan listos para recibir.
        this.fetchData(); 
        /* console.log(`3.componentDidMount`); */
        this.intervalID = setInterval(this.fetchData,5000);
       }

        fetchData =  async () => {
        //volvemos a setear loading en tru y error en null por si a futuro se vuelve a cargar
        this.setState({loading: true, error: null})
        //llamamos a la API
        try {
            this.state.data = await Api.badges.list();
            
            this.setState({loading: false, error: null});
        }
        catch (error){
            this.setState({loading: false, error: error});
        }

    }
    componentDidUpdate(prevProps,prevState) {
      /*   console.log(`5.ComponentDidUpdate`);
        console.log({
            prevProps : prevProps, prevState: prevState
        });

        console.log({props: this.props, state: this.state}); */
    }

    componentWillUnmount() {
        /* console.log(`6.ComponentWillUnmount`); */
        //llamamos a clearTimeout sobre las tareas asincronas que no se realizaron
        //para limpiar memoria y evitar errores.
        clearTimeout(this.timeoutId);
        clearInterval(this.intervalID);
    }
    //render
    render() {//va en segundo lugar
        if (this.state.loading === true && !this.state.data) {
            return <PageLoading />;
        }
        //error
        if (this.state.error) {
            return <PageError error={this.state.error}/>;
        }
       /*  console.log(`2/4.render`) */
        return(
        <React.Fragment>
               <div className="Badges">
                   <div className="Badges__hero">
                       <div className="Badges__container">
                           <img className='Badges_conf-logo' src={confLogo} alt=""/>
                       </div>
                   </div>
               </div>
        <div className="Badge__container">
            <div className="Badges__buttons">
                {/* Cambiamos el a por Link para que React Router no recargue la pagina */}
                <Link to="/badges/new" className="btn btn-primary">New Badge</Link>         
                </div>
            <div className="Badges__list">
                <div className="Badges__container">
                    <BadgesList badges={this.state.data}/>
                    
                </div>
            </div>
        </div>
        </React.Fragment>
           
        )
    
    }
}

export default Badges;