import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from '../pages/Home.js';
import BadgeNew from '../pages/BadgeNew.js';
import Badges from '../pages/Badges.js';
import BadgeEdit from '../pages/BadgeEdit.js';
import BadgeDetailsContainer from '../pages/BadgeDetailsContainer.js';
import NotFound from '../pages/Notfound.js';
import Layout from '../components/Layout.js';


//Esta vez, creamos una funcion, y no extiende de React.Component
function App() {
return (
    //agregamos BrowserRouter
    <Layout>
    <BrowserRouter>
    {/* Aqui vamos a poner las rutas de nuestras paginas */}
    {/* Como BrowserRouter solo puede tener un child component, ponemos un contenedor para encapsular las rutas */}
    {/* El swith va a tomar la direccion que ponemos en el navegador, y en funcion de eso renderiza una sola ruta  */}
    {/* Usamos exact para indicarle a React Router que tiene que buscar la ruta exacta, sino toma una validacion parcial */}
    <Switch className="">
    <Route exact path='/' component={Home} />
    <Route exact path='/badges' component={Badges} />
    <Route exact path='/badges/new' component={BadgeNew} />
    <Route exact path='/badges/:badgeID' component={BadgeDetailsContainer} />
    <Route exact path='/badges/:badgeID/edit' component={BadgeEdit} />
    <Route component={NotFound} />
    </Switch>
    </BrowserRouter>
    </Layout>
)
}

export default App;