import React from 'react';
import {Link} from 'react-router-dom';
import '../components/styles/BadgeDetails.css';
import logo from '../images/platziconf-logo.svg';
import DeleteBadgeModal from '../components/DeleteBadgeModal.js';
import Badge from '../components/badge.js';
//Custom Hook
function useIncreaseCount(max) {
    const [count, setCount] = React.useState(0);

    if(count>max) {
        setCount(0);
    }
    return [count,setCount];
}

function BadgeDetails(props) {
    //Vamos a crear un hook para manejar el contador
    //los parametros del array son similares a state y setState
    const [count, setCount] = useIncreaseCount(4);
    


    const badge = props.badge;
    return(
    <div className="">
        <div className="BadgeDetails__hero">
            <div className="container">
                <div className="row">
                    <div className="col-6">
                        <img src={logo} alt="Logo de la conferencia"/>
                    </div>
                    <div className="col-6 BadgeDetails__hero-attendant-name">
                        <h1>{badge.firstName} {badge.lastName}</h1>
                    </div>
                </div>
            </div>
        </div>
        <div className="container">
        <div className="row">
            <div className="col">
                <Badge firstName={badge.firstName} lastName={badge.lastName} jobTitle= {badge.jobTitle} email={badge.email} twitter={badge.twitter} />
            </div>
            <div className="col">
                <h2>Actions</h2>

                <div className="">
                    <button className="btn-primary btn mr-4" onClick={()=>{setCount(count+1)}}>
                        Increase Count {count}
                    </button>
                    <Link className='btn btn-primary mb-4' to={`${badge.id}/edit`}>Edit</Link></div>
                <div className=""><button onClick={props.onOpenModal} className='btn btn-danger'>Delete</button>
                <DeleteBadgeModal 
                isOpen={props.modalIsOpen} 
                onClose={props.onCloseModal}
                onDeleteBadge={props.onDeleteBadge}/>
                </div>
            </div>
        </div>
    </div>
    </div>
   
)}

export default BadgeDetails;